import React, {useEffect, useState} from "react";
import {useAuth} from "../../context/auth";
import jwt from "jwt-decode";
import ProgressResultsComponent from "./ProgressResultsComponent";
import BASE_URL from "../../baseUrl";

const ProgressResultContainer = (props) => {

    const [loggedStudent, setLoggedStudent, loggedStudentId, setLoggedStudentId] = useState()
    const [x, setX] = useState([])
    const [y, setY] = useState([])
    const {authToken, setAuthToken} = useAuth()
    var decodedToken = ""

    try {
        decodedToken = jwt(authToken)
    } catch (exception) {
        setAuthToken("")
    }



    useEffect(() => {
        fetch(BASE_URL + "/api/students/getStudent", {
            method: 'GET',
            mode: 'cors',
            headers: {
                'Accept': 'application/json, text/plain',
                'Content-Type': 'application/json',
                "Authorization": 'Bearer ' + authToken
            }
        })
            .then(response => {
                if(response.ok) {
                    return response.json()
                }
                else if(response.status === 401) {
                    setAuthToken()
                }
                else {
                    throw Error("Napotkano błąd")
                }

            })
            .then(data => {
                if(data) {
                    localStorage.setItem("student",data.id)
                    localStorage.setItem("punkty",data.points)
                    setLoggedStudent(data)
                    setLoggedStudentId(data.id)
                }
            })
            .catch(error => {
                console.log("error")
            })
    }, [authToken, setAuthToken])


    const loggedStudentPoints = (localStorage.getItem("punkty") / 99 * 100).toFixed(2);
    console.log(loggedStudentPoints)


    useEffect(() => {
        fetch(BASE_URL + "/api/students/getStudentPlotData", {
            method: 'GET',
            mode: 'cors',
            headers: {
                'Accept': 'application/json, text/plain',
                'Content-Type': 'application/json',
                "Authorization": 'Bearer ' + authToken
            }
        })
            .then(response => {
                if(response.ok) {
                    return response.json()
                }
                else if(response.status === 401) {
                    setAuthToken()
                }
                else {
                    throw Error("Napotkano błąd")
                }

            })
            .then(data => {
                if(data) {
                    setX(data.x)
                    setY(data.y)
                }
            })
            .catch(error => {
                console.log("error")
            })
    }, [authToken, setAuthToken])




    return<ProgressResultsComponent
            loggedStudent = {loggedStudent}
            loggedStudentId = {loggedStudentId}
            loggedStudentPoints = {loggedStudentPoints}
            x = {x}
            y = {y}
        />


}

export default ProgressResultContainer;