import React, {useEffect, useState} from "react";
import {Paper, TextField} from "@material-ui/core";
import word from "./word";
import {Button, Grid, IconButton, Typography, useButton} from "@mui/material";
import {TETROMINOS} from "../../tetrominos";
import * as PropTypes from "prop-types";
import {styled} from '@mui/system';
import clsx from 'clsx';

const paperStyle = {
    padding: '20px 10px',
    width: '300px',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    margin: "60px auto",
    height: '215px'
}
const textFieldStyle = {fontsize: '30px'}
const wordButtonStyle = {
    border: '2px solid #dddddd',
    borderRadius: '10%',
    padding: "5px 15px",
    margin: '10px',
    color: 'black',
    font: '20px'
}


const grammaticalBlock = {

    'ó': ['u', 'ó'],
    'ch': ['ch', 'h'],
    'ż': ['ż', 'rz'],
    'u': ['u', 'ó'],
    'h': ['ch', 'h'],
    'rz': ['ż', 'rz']

}


const blue = {
    500: '#007FFF',
    600: '#0072E5',
    700: '#0059B2',
};

const CustomButtonRoot = styled('button')`
  font-family: IBM Plex Sans, sans-serif;
  font-weight: bold;
  background-color: whitesmoke;
  width: 80px;
  height: 60px;
  border-radius: 8px;
  border: 2px solid #282c34;
  color: black;
  transition: all 150ms ease;
  cursor: pointer;


  &:hover {
    background-color: rgb(242, 205, 156);
  }

  &:active {
    background-color: rgb(248, 186, 101);
  }


  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
`;

const CustomButton = React.forwardRef(function CustomButton(props, ref) {
    const {children} = props;
    const {active, disabled, focusVisible, getRootProps} = useButton({
        ...props,
        ref,
        component: CustomButtonRoot,
    });

    const classes = {
        active,
        disabled,
        focusVisible,
    };

    return (
        <CustomButtonRoot {...getRootProps()} className={clsx(classes)}>
            {children}
        </CustomButtonRoot>
    );
});

CustomButton.propTypes = {
    children: PropTypes.node,
};


const Gap = (props) => {
    const {
        answers, givenAnswer,
        setGivenAnswer, gameOver, setAnswers
    } = props

    const signButtonClick = (sign) => {

        setGivenAnswer(true);

        if (sign === word.answer) {
            word.points = word.points + 1
            word.correct = word.correct + 1

            // info o odpowiedzi pozytywnej
            setAnswers(prevState => ({
                    ...prevState,
                    word: [...prevState.word, {
                        word: word.word,
                        correct: true
                    }]
                })
            )

            TETROMINOS['slowa'] = TETROMINOS['slowa'].filter(item => item.word !== word.word)

            console.log('TETROMINOS ', TETROMINOS.slowa)

            if (TETROMINOS.slowa.length <= 0) {
                props.setGameOver(true)
                props.setDropTime(null)
                console.log('GAME OVER HERE')
            } else {
                props.resetPlayer()
            }
        } else {
            word.points = word.points > 0 ? word.points - 1 : word.points;
            word.wrong = word.wrong + 1

            // info o odpowiedzi negatywnej
            setAnswers(prevState => ({
                    ...prevState,
                    word: [...prevState.word, {
                        word: word.word,
                        correct: false
                    }]
                })
            )
        }
    }
    // style={wordButtonStyle}

    const getSignButton = () => {
        return Object.keys(grammaticalBlock).map((sign, index) => (
            <Grid display={"flex"} justifyContent={'center'} item key={index} xs={4}>
                <CustomButton disabled={gameOver !== false || !grammaticalBlock[word.answer]?.includes(sign) || givenAnswer}
                              color={'primary'} variant="outlined" size={"large"}
                              onClick={() => gameOver === false && signButtonClick(sign)}>
                    <Typography style={{fontSize: '2rem'}}>{sign}</Typography>
                </CustomButton>
            </Grid>
        ))
    }

    // console.log(props.callback)
    return (
        <div className="gap">
            {/*<form onSubmit={props.handleSubmit}>*/}
            <Paper elevation={3} style={paperStyle}>
                <h3 style={{marginBottom: '30px'}}>Wybierz odpowiednią literę</h3>

                <Grid container style={{height: '100%', justifyContent: 'space-between'}}>
                    {getSignButton()}
                </Grid>

            </Paper>
            {/*</form>*/}
        </div>
    )

}

export default Gap;
