import word from './components/game/word'
import {useAuth} from "./context/auth";
import loggedId from "./constants";
import BASE_URL from "./baseUrl";


export let TETROMINOS = {
    0: {shape: [[0]], color: '0, 0 , 0', word: ''},
    I: {
        shape: [
            ['I'],
        ],
        color: '0, 0, 0',
        word: 'cell2',

    },
    isLoaded: false,

    slowa: undefined
}


export const randomTetromino = () => {

    const slowo = TETROMINOS.slowa?.pop();
    TETROMINOS['I'].word = slowo
    word.word = slowo?.word
    word.answer = slowo?.answer

    return TETROMINOS['I'];
}

export const getWordTetromino = async () => {

    const token = localStorage.getItem("token")
    const response = await fetch(BASE_URL + `/answers/getAll`, {
        method: 'GET',
        mode: "cors",
        headers: {
            'Accept': 'application/json, text/plain',
            'Content-Type': 'application/json',
            "Authorization": 'Bearer ' + token
        }
    })
    if (response.ok)
        return response.json();

    throw new Error('problem with data');

}

