import React from "react";
import {Paper} from "@mui/material";
import {FcUpLeft} from "react-icons/fc";
import {Link} from "react-router-dom";
import {List, ListItem, ListItemText} from "@material-ui/core";
import {ListOutlined} from "@mui/icons-material";
import rd3 from 'react-d3-library';
import {Plots} from "plotly.js";
import Plot from 'react-plotly.js';

const paperStyle = {padding: '50px 10px', width: 'auto', margin: "60px auto"}


const ProgressResultsComponent = (props) => {


    let counter = 0;

    let valX = [];
    props.x.map((obj) => {
        counter = counter + 1
        valX.push(counter)
    })

    let valY = [];
    props.y.map((obj) => {
        valY.push(obj)
    })


    console.log(valX)








    return (
        <div className="home ranking-container">
            <h1 className="h1-background"> Moje wyniki</h1>
            <Link style={{color: 'black'}} to='/home'> <span><FcUpLeft/></span>Powrót do strony głównej </Link>
            <Paper style={paperStyle} elevation={3}>
                <List className="list-ranking">
                    <ListItem
                        key={props.loggedStudent?.id}>
                        <ListItemText className="list-icon3" primary="Całkowita liczba punktów:"/>
                        <ListItemText className="list-icon2" primary={` ${props.loggedStudent?.points}`} />
                    </ListItem>
                    <ListItem>
                        <ListItemText className="list-icon3" primary="Opanowane słówka (w %):"/>
                        <ListItemText className="list-icon2" primary={` ${props.loggedStudentPoints}`} />
                    </ListItem>
                </List>
            </Paper>
            <Plot
                data={[
                    {type: 'bar', x: valX, y: valY, marker: {color: '#9c4dcc'}},
                ]}
                layout={ {width: 420, height: 440, title: 'Punkty zdobyte w pojedynczych podejściach'} }
            />
        </div>
    )

}

export default ProgressResultsComponent;
