import turtle from '../../images/15-153751_tortoise-silhouette-icons-png-george-washington-silhouette-clip.png'
import styled from "styled-components";


import {StyledGame, StyledGameWrapper} from "../styles/StyledGame";

//custom Hooks
import {useInterval} from "./hooks/useInterval";
import {usePlayer} from "./hooks/usePlayer";
import {useStage} from "./hooks/useStage";
import {createStage, checkCollision} from "../../gameHelpers";
import {getWordTetromino, TETROMINOS} from "../../tetrominos";
import {useAuth} from "../../context/auth";
import {Slider, Stack} from "@mui/material";
import word from '../game/word'
import {VolumeDown, VolumeUp} from "@mui/icons-material";

const dialogStyle = {display: 'flex', margin: '0 0 20px 0', padding: '20px', border: '4px dolid #333', width: '100%'}


// box-sizing: border-box;
// display: flex;
// align-items: center;
