const Game = (props) => {
    const [dropTime, setDropTime] = useState(null);
    const [gameOver, setGameOver] = useState(undefined);
    const [givenAnswer, setGivenAnswer] = useState(false);
    const [speed, setSpeed] = useState(50);
    const {authToken} = useAuth()

    const [player, updatePlayerPos, resetPlayer, playerRotate] = usePlayer();
    const [stage, setStage] = useStage(player, resetPlayer);

    const [callbackButton, setCallbackButton] = useState(false);


    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    useEffect(() => gameOver && setOpen(true), [gameOver])

    useEffect(() => {

        if (gameOver === false && !! word.word) {
            setGivenAnswer(false);
            const msg = new SpeechSynthesisUtterance();
            msg.text = word.word?.replace('_', word.answer);
            window.speechSynthesis.speak(msg);
        }

    }, [word.word])


    const [answers, setAnswers] = useState({
        studentId: sessionStorage.getItem('student'),
        word: []
    })


    // console.log('re-render');

    const movePlayer = dir => {
        if (!checkCollision(player, stage, {x: dir, y: 0})) {
            updatePlayerPos({x: dir, y: 0});
        }
    }

    useEffect(() => {
        if (gameOver) {
            sendAnswer(answers?.word, authToken)
            console.log(authToken);
        }
        // W answers masz wszystko co chcialas wyslac

        // TU JEST MIESJCE NA TWOJE ZAPYTANIE Z WYNIKAMI
        // fetch(`API`, {
        //     method: 'POST',
        //     mode: "cors",
        //     headers: {
        //         'Accept': 'application/json, text/plain',
        //         'Content-Type': 'application/json',
        //         "Authorization": 'Bearer ' + sessionStorage.getItem('token')
        //     }
        // })
        //     .then(res=>{
        //         if(!res.ok)
        //             throw new Error('something wnet wrong')
        //     })
        //     .catch(e=>{
        //         console.log('ERROR getWordTetromino: ',e)
        //     })

    }, [gameOver])

    const resetWord = () => {
        word.word = ''
        word.answer = ''
        word.points = 0
        word.wrong = 0
        word.correct = 0
        word.tempo = 1500 - 10 * speed
    }

    const startGame = () => {
        //reset
        getWordTetromino().then(data => {

                TETROMINOS.slowa = data;

                setGameOver(false);
                word.init()
                setAnswers({
                    studentId: loggedId,
                    word: []
                })
                resetWord()
                setStage(createStage());
                setCallbackButton(true);

                setDropTime(word.tempo);
                resetPlayer();
                console.log('test')


            }
        )

    }

    const drop = () => {
        if (!checkCollision(player, stage, {x: 0, y: 1})) {
            updatePlayerPos({x: 0, y: 1, collided: false})
        } else {
            if (player.pos.y < 1 || gameOver) {
                console.log("GAME OVER");
                setGameOver(true);
                setDropTime(null);
            }
            updatePlayerPos({x: 0, y: 0, collided: true});
        }
    }

    const onChangeSpeedHandler = useCallback((event, value) => setSpeed(value), [setSpeed])

    const dropPlayer = () => {
        drop();
    }

    useInterval(() => {
        drop();
    }, dropTime)


    return (
        <StyledGameWrapper role="button" tabIndex="0">
            <StyledGame>
                <Stage stage={stage}/>
                <aside style={{width:'85%'}}>
                    <StartButton callback={startGame}/>
                    {gameOver ? (
                        <>
                            <Display gameOver={gameOver} text="Koniec gry"/>
                            {/*<Display gameOver={gameOver} text={`Uzyskano: ${word.points} pkt`}/>*/}
                            <Display gameOver={gameOver} text={`Poprawnych: ${word.correct}`}/>
                            <Display gameOver={gameOver} text={`Niepoprawnych: ${word.wrong}`}/>
                            <Stack spacing={2} direction="row" sx={{mb: 1}} alignItems="center">
                                <img style={{width: '70px', height: '40px'}} srcSet={turtle} alt={'slow turtle'}/>
                                <Slider onChange={onChangeSpeedHandler} value={speed} disabled={gameOver === false}
                                        color={'secondary'} style={{width: '100px'}}/>
                                <img style={{width: '60px', height: '60px'}} srcSet={rabbit} alt={'quick rabbit'}/>
                            </Stack>

                        </>
                    ) : (
                        <div style={{
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'center'
                        }}>
                            {/*<Display text={`Punkty: ${word.points}`}/>*/}
                            <Display text={`Poprawnych: ${word.correct}`}/>
                            <Display text={`Błędne: ${word.wrong}`}/>
                            <Stack spacing={2} direction="row" sx={{mb: 1}} alignItems="center">
                                <img style={{width: '70px', height: '40px'}} srcSet={turtle} alt={'slow turtle'}/>
                                <Slider onChange={onChangeSpeedHandler} value={speed} disabled={gameOver === false}
                                        color={'secondary'} style={{width: '100px'}}/>
                                <img style={{width: '60px', height: '60px'}} srcSet={rabbit} alt={'quick rabbit'}/>
                            </Stack>
                            <GapContainer
                                givenAnswer={givenAnswer}
                                setGivenAnswer={setGivenAnswer}
                                setDropTime={setDropTime}
                                gameOver={gameOver}
                                setGameOver={setGameOver}
                                answers={answers}
                                setAnswers={setAnswers}
                                resetPlayer={resetPlayer}
                                callbackButton={callbackButton}/>
                        </div>
                    )}
                    <Dialog
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description"
                    >
                        <DialogTitle id="alert-dialog-title">
                            {"Skończyłem grać! Co dalej?"}
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                Gratulacje! Właśnie zakończyłeś rozgrywkę :) Możesz przejść do kategorii "Moje postępy"
                                i dokładniej zapoznać się ze swoimi wynikami. Powodzenia w następnej rozgrywce!
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button color="secondary" variant="text" onClick={handleClose}>Rozumiem</Button>
                        </DialogActions>
                    </Dialog>
                </aside>
            </StyledGame>
        </StyledGameWrapper>
    );
};

export default Game;
