import React, {useState} from 'react';
import SideNavigationComponent from '../navigation/SideNavigationComponent'
import Game from "../game/Game";
import {Slider} from "@mui/material";

const homeStyle = { width: 'auto', height: 'auto', color: 'red'}

const HomePageContainer = (props) => {

        return(
            <div className="home">
                    <div className="home-wrapper">
                            <div className="column-one-wrapper">
                                <SideNavigationComponent/>
                                    <>{props.content}</>
                            </div>
                        <div className="column-two-wrapper">
                            <div className="title">
                                <h1 className="h1-background h1-title">Zabawa z ortografią</h1>
                            </div>

                        <Game/>

                        </div>

                    </div>
            </div>
        )
}

export default HomePageContainer